* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  background: #ffd8d0;
  position: relative;
}

a {
  display: inline-block;
  text-decoration: none;
  color: #000;
}

ul,
li {
  list-style: none;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

.container {
  padding: 0 10%;
}

section {
  width: 100%;
  height: 100vh;
}

header {
  width: 100%;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  border-bottom: 2px solid #000;
  display: flex;
  align-items: center;
}
header nav {
  width: 100%;
  height: inherit;
  display: flex;
  align-items: center;
}
header nav .logo-image__wrapper {
  display: flex;
  align-items: center;
  border-right: 2px solid #000;
  height: inherit;
}
header nav .logo-image__wrapper .logo-image {
  width: 120px;
  margin-right: 100px;
}
header nav .navbar {
  display: flex;
  margin-left: 100px;
  justify-content: space-between;
}
header nav .navbar .menu {
  display: flex;
}
header nav .navbar .menu li {
  padding: 0 30px;
  border-right: 2px solid #000;
}
header nav .navbar .menu li:nth-of-type(4) {
  border-right: none;
}
header nav .navbar .utility {
  display: flex;
}

#hero .wrapper {
  padding-top: 300px;
}
#hero .wrapper .main-text {
  font-size: 60px;
  font-weight: bold;
  letter-spacing: 1.5px;
  line-height: 1.25;
  margin-bottom: 40px;
}
#hero .wrapper .main-text span {
  display: block;
}
#hero .wrapper .main-text span:nth-of-type(3) {
  color: #fe665e;
}
#hero .wrapper .sub-text {
  color: #808080;
}
#hero .wrapper .sub-text span {
  display: block;
}